import { createModel } from '@souche-f2e/uniqlo';
import { fetchUser } from '@/services/global';

const initState = {
  currentUser: null,
};

export default createModel({
  // model name model 
  name: 'global',
  // state
  // model 
  state: {
    //   https://developer.mozilla.org/zh-CN/docs/Web/JavaScript/Reference/Operators/Spread_syntax
    ...initState,
  },
  // effects
  // **
  //  IO 
  // ** effect  state
  //  effect  reducer  state
  //  camelCase
  effects: {
    async loadUser(payload, rootState) {
      const user = await fetchUser();
      this.SET_STATE({ currentUser: user });
    },
  },
  // reducers
  // state**
  //  immer  reducer  state  state
  // constants case
  reducers: {
    RESET_STATE(state, payload) {
      return { ...initState };
    },
    SET_STATE(state, payload) {
      Object.keys(payload).forEach((key) => {
        if ((initState[key] !== undefined) && (payload[key] !== undefined)) {
          state[key] = payload[key];
        }
      });
    },
  },
});
