import React, {Component} from 'react'
import {Modal,Row, Col,Button } from 'antd'
import {connect} from '@souche-f2e/uniqlo'

class Info extends Component{


  handleCancel = () => {
    this.props.SET_STATE({
      Infovisible: false
    })
  }

  render () {
    const {
      mirrorNo,
      statusName,
      produceDate,
      inputRemark,
      batchNo,
      inputDate,
      purchaseRemark,
      salesChannelName,
      salesOrgName,
      salesDate,
      salesRemark,
      scrapDate,
      scrapName,
      orgName,
      vin,
      carPlateNo,
      installDate,
      installRemark,
      bindUserMobile,
      activeDate,
    } = this.props.infoObj
    return (
      <div>
        <Modal
          width='850px'
          title=""
          visible={this.props.Infovisible}
          onCancel={this.handleCancel}
          footer={[
            <Button key="back" onClick={this.handleCancel}></Button>,
          ]}
        >
       <div style={{marginBottom:'20px'}}>
       <h3></h3>
        <Row>
          <Col span={12}><div>ID:{mirrorNo} </div></Col>
          <Col span={12}><div>:{statusName}</div></Col>
          <Col span={12}><div>: {produceDate}</div></Col>
          <Col span={12}><div>:{inputRemark}</div></Col>
        </Row>
       </div>

        <div style={{marginBottom:'20px'}}>
        <h3></h3>
        <Row>
          <Col span={12}><div>:{batchNo} </div></Col>
          <Col span={12}><div>:{inputDate}</div></Col>
          <Col span={12}><div>:{purchaseRemark} </div></Col>
        </Row>
        </div>

        <div style={{marginBottom:'20px'}}>
        <h3></h3>
        <Row>
          <Col span={12}><div>:{salesChannelName} </div></Col>
          <Col span={12}><div>:{salesOrgName}</div></Col>
          <Col span={12}><div>:{salesDate} </div></Col>
          <Col span={12}><div>:{salesRemark} </div></Col>
        </Row>
        </div>

       <div style={{marginBottom:'20px'}}>
       <h3></h3>
        <Row>
          <Col span={12}><div>:{scrapDate} </div></Col>
          <Col span={12}><div>:{scrapName}</div></Col>
        </Row>
       </div>

       <div style={{marginBottom:'20px'}}>
       <h3></h3>
        <Row>
          <Col span={12}><div>:{orgName} </div></Col>
          <Col span={12}><div>:</div></Col>
          <Col span={12}><div>:{purchaseRemark}</div></Col>
        </Row>
       </div>

        <div style={{marginBottom:'20px'}}>
        <h3></h3>
        <Row>
          <Col span={12}><div>:{vin} </div></Col>
          <Col span={12}><div>:{carPlateNo}</div></Col>
          <Col span={12}><div>:{installDate}</div></Col>
          <Col span={12}><div>:{installRemark}</div></Col>
        </Row>
        </div>

        <h3></h3>
        <Row>
          <Col span={12}><div>:{bindUserMobile} </div></Col>
          <Col span={12}><div>:{activeDate}</div></Col>
        </Row>
        </Modal>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  const {infoObj,Id,Infovisible} = state.cloudMirrorPool
  return {infoObj,Id,Infovisible}
}

const mapDispatchToProps = (dispatch) => {
  const {SET_STATE} = dispatch.cloudMirrorPool
  return {SET_STATE}
}

export default connect (mapStateToProps, mapDispatchToProps) (Info)
