import React, {Component} from 'react'
import {Table,Divider,Pagination,Spin} from 'antd'
import { connect } from '@souche-f2e/uniqlo';

class Tab extends Component {
  pageChange = (current) => { //
    let data = {...this.props.data, page:current}
    this.props.QueryCloudMirrorInvDetail(data)
  }
  onShowSizeChange = (current, pageSize) => {
    let data = {...this.props.data, page:current, pageSize:pageSize}
    this.props.QueryCloudMirrorInvDetail(data)
  }
  InfoShow = (record) => {//
    this.props.SET_STATE({
      Infovisible: true,
    })
    this.props.QueryCloudMirrorInvById({id: record.id})
  }
  MemarryShow = (record) => { //
    this.props.SET_STATE({
      Memarryvisible: true,
      loading: true,
      IntObj: {...this.props.IntObj, page: 1, mirrorId: record.id}
    })
    this.props.PageCloudMirrorLogDetail({...this.props.IntObj, mirrorId: record.id})
  }

  render () {
    const columns = [
      {
      title: '',
      width:200,
      key:'infoy',
      render: (record) => (
        <div>
          <div>ID:{record.mirrorNo}</div>
          <div>:{record.statusName}</div>
          <div>:{record.produceDate}</div>
          <div>:{record.inputDate}</div>
        </div>
      )
    }, {
      title: '',
      width:400,
      key:'info',
      render: (record) => (
        <div>
          <div>:{record.isSales==1?'':''}</div>
          <div>:{record.salesChannelName}</div>
          <div>:{record.salesOrgName}</div>
          <div>:{record.salesDate}</div>
        </div>
      )
    },
    {
      title: '',
      width:400,
      key:'reative',
      render: (record) => (
        <div>
          <div>:{record.orgName}</div>
          <div>:{record.inputDate}</div>
        </div>
      )
    }, {
      title: '',
      width:400,
      key:'carj',
      render: (record) => (
        <div>
          <div>:{record.isInstall==1?'':''}</div>
          <div>:{record.carPlateNo}</div>
          <div>:{record.vin}</div>
          <div>:{record.installDate}</div>
        </div>
      )
    },
    {
      title: '',
      key:'people',
      render: (record) => (
        <div>
          <div>:{record.isActive==1?'':''}</div>
          <div>:{record.bindUserName}</div>
          <div>:{record.bindUserMobile}</div>
          <div>:{record.activeDate}</div>
        </div>
      )
    }, {
      title: '',
      fixed: 'right',
      key:'action',
      width:140,
      render: (record) => (
        <div><a onClick={this.InfoShow.bind(this, record)} href="javascript:;"></a>
         <Divider type="vertical" />
        <a onClick={this.MemarryShow.bind(this, record)} href="javascript:;"></a></div>
      )
    }
  ];
    return (
      <div>


        <Spin spinning={this.props.searchloading} >
        <Table
        columns={columns}
        pagination={false}
        scroll={{ x: 1900 }}
        bordered
        dataSource={this.props.tableList} />
          </Spin>

<div style={{textAlign:'center', marginTop: '20px'}}>
      <Pagination
        size='small'
        showQuickJumper
        showSizeChanger
        onShowSizeChange={this.onShowSizeChange}
        total={+this.props.totalNumber}
        showTotal={total => ` ${total} `}
        pageSize={this.props.data.pageSize}
        current={this.props.data.page}
        onChange={this.pageChange} />
      </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  const {searchloading,loading,IntObj,Id,data,tableList, totalNumber,Infovisible,Memarryvisible} = state.cloudMirrorPool
  return {searchloading,loading,IntObj,Id,data,tableList,totalNumber,Infovisible,Memarryvisible}
}

const mapDispatchToProps = (dispatch) => {
  const {QueryCloudMirrorInvDetail,PageCloudMirrorLogDetail,QueryCloudMirrorInvById,SET_STATE} = dispatch.cloudMirrorPool
  return {QueryCloudMirrorInvDetail,PageCloudMirrorLogDetail,QueryCloudMirrorInvById,SET_STATE}
}

export default connect (mapStateToProps,mapDispatchToProps) (Tab)
